@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap);
:root{
  --primary-color: #4B43A3;
  --active-btn-color: #48A59D;
  --text-color: #232340;
  --dark-grey-color: #8C8A9F;
  --light-grey-color: #E3E0F3;
  --purple-color: #726EBC;
  --white-color: #fefefe;
}

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body{
  height: 120vh;
  background-color: #4B43A3;
  background-color: var(--primary-color);
}

main{
  max-width: 100%;
  color: #232340;
  color: var(--text-color);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-family: 'Poppins', sans-serif;
}

.container{
  padding: 1rem;
  margin: 1rem 2rem;
  background-color: #fefefe;
  background-color: var(--white-color);
  border-radius: 1rem;
}

.btn-container{
  border: 3px solid #E3E0F3;
  border: 3px solid var(--light-grey-color);
  padding: 1rem;
  border-radius: 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.item-btn{
  padding: 0.8rem;
  width: 100%;
  font-family: 'Poppins', sans-serif;
  font-size: 1rem;
  text-transform: capitalize;
  font-weight: 600;
  border-radius: 1rem;
  outline: none;
  border: none;
  background-color: transparent;
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.active-btn{
  background-color: #48A59D;
  background-color: var(--active-btn-color);
  color: #fefefe;
  color: var(--white-color);
}

.info-container{
  margin: 1.5rem 2rem;
}

h4{
  color: #8C8A9F;
  color: var(--dark-grey-color);
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: 600;
}

.title{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  text-transform: capitalize;
  font-size: 0.8rem;
  margin: 0.5rem 0 1rem 0;
  font-weight: 800;
}

p{
  font-weight: 600;
}

.list-div p{
  margin: 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.check-mark{
  color: #4B43A3;
  color: var(--primary-color);
  width: 1rem;
  height: 1rem;
}

.cart-btn{
  width: 100%;
  padding: 1rem;
  font-size: 1rem;
  background-color: #4B43A3;
  background-color: var(--primary-color);
  outline: none;
  border: none;
  border-radius: 1rem;
  font-weight: 600;
  color: #fefefe;
  color: var(--white-color);
  margin-bottom: 1rem;
}

@media screen and (min-width: 480px) {
  body{
    height: 0;
  }
  main{
    height: 100vh;
  }
  .container{
    min-height: 70vh;
  }
  .btn-container{
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
  }
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

